<template>
  <l-map
    ref="map"
    style="height: 100%; z-index: 0;"
    :zoom="getZoom"
    :center="getCenter"
    :center-control="true"
    :options="{zoomControl: false}"
    @update:zoom="zoomUpdate"
  >
  <!-- <l-control-layers position="topright" :collapsed="true" :sort-layers="true"></l-control-layers> -->
  
  <l-control-scale position="topright" :imperial="false" :metric="true"></l-control-scale>
  <l-control-zoom position="bottomright"  ></l-control-zoom>
  <l-control-center position="bottomright"></l-control-center>
  <!-- <l-control-layers position="bottomright"  ></l-control-layers> -->
  
    <l-control position="bottomleft" >

      <v-card class="legend">
        <v-expansion-panels>
          <v-expansion-panel>
            <v-expansion-panel-header>
              <h4>Legend</h4>
              <template v-slot:actions>
                <v-icon>mdi-chevron-up</v-icon>
              </template>
            </v-expansion-panel-header>
            <v-expansion-panel-content>
               <!-- <hr> -->
              <v-layout row wrap>
                <v-flex
                  :class="legend.expanded ? `xs12 my-1` : `xs6 my-1`"
                  v-for="legend in colors"
                  :key="legend.name"
                >
                  <v-chip outlined>
                    <v-avatar left>
                      <v-icon :color="legend.color">mdi-circle</v-icon>
                    </v-avatar>
                    {{ legend.name }}
                  </v-chip>
                </v-flex>
              </v-layout>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </v-card>
    </l-control>


    <!-- Recenter Map -->
    <l-control 
    position="bottomleft" 
    ><v-chip  @click="reset_map">
    <h4>Reset Map</h4>
    </v-chip> </l-control>


     <l-control position="topleft" class="my-5 mx-7">
     <v-chip
        class="d-flex justify-center"
        style="width:135px;"
        color="white"
        @click="toggleRightDrawer"
        v-if="this.$store.state.leftDrawer == false"
      >Filter Menu</v-chip>
    </l-control>

    <!-- <l-control position="topleft" class="my-1 mx-7">
     <v-chip
        style="width:135px;"
        color="white"
        @click="toggleLeftDrawer"
        v-if="this.$store.state.leftDrawer == false"
      >Summary Viewer</v-chip>
    </l-control> -->

  

    <l-control position="topright" class="my-2 mx-7">
      <v-autocomplete
        hide-details
        rounded
        color="black"
        background-color="white"
        style="width:200px;"
        :items="PlotList"
        v-model="plotID"
        label="Search Plot"
        @change="fetchPlotInfo_byselect()"
