      "getInfoDialog",
      "getLayerVisibility",
      "getPlotVisibility",
      "getUtilityVisibility",
      "getWS_Visibility",
      "getEL_Visibility",
      "getDR_Visibility",
    ])
  },
  methods: {
    toggleRightDrawer() {
      this.$store.commit("setRightDrawer", !this.getRightDrawer);
    },
    toggleLeftDrawer() {
      this.$store.commit("setLeftDrawer", !this.getLeftDrawer);
    },
    zoomUpdate() {
      this.$store.commit("setZoom", this.$refs.map.mapObject.getZoom());
      // console.log("Zoom updated: ", this.$refs.map.mapObject.getZoom());
    },
     toggleInfoDialog() {
      this.$store.commit("setInfoDialog", !this.getInfoDialog);
    },
    
 

    khasraOptions(khasra) {
      return {
        onEachFeature: function onEachFeature(feature, layer) {
          if (khasra) {
            layer.bindTooltip(`
                  <div>
                    <h4>Khasra No: ${khasra.properties.KhasraNo}</h4>
                    <hr>
                    <h4>Status: ${khasra.properties.status}</h4>
                  </div>
                  
            `,
            {sticky: true }
            );
          }
        }
      };
    },

    pipelineOptions(pipeline) {
      return {
        onEachFeature: function onEachFeature(feature, layer) {
          if (pipeline) {
            layer.bindTooltip(`
                  <div>
                    <h4>Pipe No: ${pipeline.properties.pipe_no}</h4>
                    <hr>
                    <h4>Diameter (Inches): ${pipeline.properties.PipDiamINC}</h4>
                    <hr>
                    <h4>Length (Feet): ${pipeline.properties.PipLengFT}</h4>
                    <hr>
                    <h4>Material: ${pipeline.properties.Mat_Class}</h4>
                  </div>
                  
            `,
            {sticky: true }
            );
          }
        }
      };
    },

   
 

    EL_CablesAllOptions(EL_CablesAll) {
      return {
        onEachFeature: function onEachFeature(feature, layer) {
          if (EL_CablesAll) {
            layer.bindTooltip(`
                  <div>
                    <h4>${EL_CablesAll.properties.Layer}</h4>
                  
                  </div>
                  
            `,
            {sticky: true }
            );
          }
        }
      };
    },

    

    DR_Drainage_PipesOptions(DR_Drainage_Pipes) {
      return {
        onEachFeature: function onEachFeature(feature, layer) {
          if (DR_Drainage_Pipes) {
            layer.bindTooltip(`
                  <div>
                    <h3> Pipe ID: ${DR_Drainage_Pipes.properties.ID}</h3>
                    <hr>
                    <h4>Layer: ${DR_Drainage_Pipes.properties.Layer}</h4>
                    <hr>
                    <h4>Pipe Width: ${DR_Drainage_Pipes.properties.Pipe_W}</h4>
                    <hr>
                    <h4>Pipe Material: ${DR_Drainage_Pipes.properties.Pipe_M}</h4>
                    
                  </div>
                  
            `,
            {sticky: true }
            );
          }
        }
      };
    },

    

    DR_NalaOptions(DR_Nala) {
      return {
        onEachFeature: function onEachFeature(feature, layer) {
          if (DR_Nala) {
            layer.bindTooltip(`
                  <div>
                   
                    <h4>${DR_Nala.properties.Layer}</h4>
                    
                  </div>
                  
            `,
            {sticky: true }
            );
          }
        }
      };
    },
    
