      return {
        onEachFeature: function onEachFeature(feature, layer) {
          if (plot_approved) {
            layer.bindTooltip(`
                  <div>
                    <h4>Plot ID: ${plot_approved.properties.plot_id}</h4>
                    </div>
                  
            `,
            {sticky: true }
            );
          }
        }
      };
    },

    

    utilities_approvedOptions(utilities_approved) {
      return {
        onEachFeature: function onEachFeature(feature, layer) {
          if (utilities_approved) {
            layer.bindTooltip(`
                  <div>
                    <h4>Utility: ${utilities_approved.properties.category}</h4>
                    </div>
                  
            `,
            {sticky: true }
            );
          }
        }
      };
    },

    


    style(feature, params) {
      if (feature == "plot") {
        return {
          strokeColor: "#333333",
          strokeWeight: 0.5,
          weight: 0.5,
          color: "#333333",
          opacity: 1,
          fillColor:
            params.dimensions != null
              ? this.pdfColors[params.dimensions]
              : "#ffb482",
          fillOpacity: 1
        };
      }
    },


    khasrastyle(feature, khasra) {
      if (feature== 'khasra') {
        return {
          strokeColor: "#333333",
          strokeWeight: 1.5,
          weight: 1.0,
          color: "#333333",
          opacity: 1,
          fillColor:
            khasra.properties.status != null
              ? this.pdfColors[khasra.properties.status]
              : "#ffb482",
          fillOpacity: 0.7,
         
        };
      }
    },

  pipelinestyle(feature, pipeline) {
      if (feature== 'pipeline') {
        return {
          strokeColor: "#333333",
          strokeWeight: 1.5,
          weight: 5.0,
          color: 
            pipeline.properties.PipDiamINC != null
              ? this.pdfColors[pipeline.properties.PipDiamINC]
              : "#ffb482",
          opacity: 1,
          fillColor:
            pipeline.properties.PipDiamINC != null
              ? this.pdfColors[pipeline.properties.PipDiamINC]
              : "#333333",
          fillOpacity: 1,
         
        };
      }
    },
    
    
    

    EL_CablesAllstyle(feature, EL_CablesAll) {
      if (feature== 'EL_CablesAll') {
        return {
          strokeColor: "#333333",
          strokeWeight: 1.5,
          weight: 3.0,
          color: 
            EL_CablesAll.properties.Layer != null
              ? this.pdfColors[EL_CablesAll.properties.Layer]
              : "#ffb482",
          opacity: 1,
          fillColor: "#333333",
          fillOpacity: 1,
         
        };
      }
    },
   
     DR_Drainage_Pipesstyle(feature, DR_Drainage_Pipes) {
      if (feature== 'DR_Drainage_Pipes') {
        return {
          strokeColor: "#333333",
          strokeWeight: 1.5,
          weight: 3.0,
          color: 
            DR_Drainage_Pipes.properties.Layer != null
              ? this.pdfColors[DR_Drainage_Pipes.properties.Layer]
              : "#ffb482",
          opacity: 1,
          fillColor: "#333333",
          fillOpacity: 1,
         
        };
      }
    },

    DR_Nalastyle(feature, DR_Nala) {
      if (feature== 'DR_Nala') {
        return {
          strokeColor: "#333333",
          strokeWeight: 1.5,
          weight: 2.0,
          color: "#333333",
          opacity: 1,
          fillColor:
              DR_Nala.properties.Layer != null
              ? this.pdfColors[DR_Nala.properties.Layer]
              : "#ffb482",
          fillOpacity: 1,
         
