    utilities_approvedstyle(feature, utilities_approved) {
      if (feature== 'utilities_approved') {
        return {
          strokeColor: "#333333",
          strokeWeight: 0.5,
          weight: 1.5,
          color: "#333333",
          opacity: 1,
          fillColor:
            utilities_approved.properties.category != null
              ? this.pdfColors[utilities_approved.properties.category]
              : "#92919c",
          fillOpacity: 1,
         
        };
      }
    },


    reset_map () {
      
        this.$refs.map.mapObject.flyTo(
        [33.5133, 72.8965],
        15
      );
      
    },



    fetchPlotInfo(plot_approved) {
      // var selected_record=plot_approved;
      // console.log("selected_record",selected_record);
      var polygon = plot_approved.geometry;
      var center = turf.centroid(polygon);
      this.$refs.map.mapObject.flyTo(
        [center.geometry.coordinates[1], center.geometry.coordinates[0]],
        18
      );
      this.$store.dispatch("fetchPlotInfo", {
        // url: this.api.url,
        // id: plot.new_id
        info: plot_approved,
      });
      // this.$store.commit("setPlotInfoSkeleton", true);
      this.$store.commit("setInfoDialog", !this.getInfoDialog);

      this.$store.dispatch("markerpoint", {
        lat: center.geometry.coordinates[1],
        lon: center.geometry.coordinates[0],
      });
    },

    fetchKhasraInfo(khasra) {
      // var selected_record=plot_approved;
      // console.log("selected_record",selected_record);
      var polygon = khasra.geometry;
      var center = turf.centroid(polygon);
      this.$refs.map.mapObject.flyTo(
        [center.geometry.coordinates[1], center.geometry.coordinates[0]],
        18
      );
      this.khasra_dialog=true;
      // this.$store.dispatch("fetchPlotInfo", {
      //   // url: this.api.url,
      //   // id: plot.new_id
      //   info: plot_approved,
      // });
      // this.$store.commit("setPlotInfoSkeleton", true);
      // this.$store.commit("setInfoDialog", !this.getInfoDialog);

      this.$store.dispatch("markerpoint", {
        lat: center.geometry.coordinates[1],
        lon: center.geometry.coordinates[0],
      });
    },



    


    fetchPlotInfo_byselect() {
     var plot = this.map.plots_approved.features.filter(item => { 
     if(this.plotID === item.properties.plot_id) { 
          return item;
      }
        });
    var geojson = plot[0].geometry;
    var ring = geojson.coordinates[0][0];
    var poly = turf.polygon([ring]);
    var centroid = turf.centroid(poly);
    // console.log("centroid:",centroid)

    this.$refs.map.mapObject.flyTo(
        [centroid.geometry.coordinates[1], centroid.geometry.coordinates[0]],
        18
      );
          this.$store.dispatch("markerpoint", {
        lat: centroid.geometry.coordinates[1],
        lon: centroid.geometry.coordinates[0],
      });
     },

    

    fetchKhasraInfo_byselect() {
     var khasra = this.map.khasras.features.filter(item => { 
     if(this.khasraID === item.properties.KhasraNo) { 
          return item;
      }
        });
    var geojson = khasra[0].geometry;
    var ring = geojson.coordinates[0][0];
    var poly = turf.polygon([ring]);
    var centroid = turf.centroid(poly);
    // console.log("centroid:",centroid)

    this.$refs.map.mapObject.flyTo(
        [centroid.geometry.coordinates[1], centroid.geometry.coordinates[0]],
        18
      );
          this.$store.dispatch("markerpoint", {
        lat: centroid.geometry.coordinates[1],
        lon: centroid.geometry.coordinates[0],
      });


    },


  },
  
  mounted() {
    this.$store.commit("setMap", this.$refs.map.mapObject);

  }
};
